.styled-table{
    border-collapse: collapse;
    margin:auto;
    font-size: 0.9em;
    font-family:sans-serif;
    max-width: 800px;
    box-shadow: 0 0 20px rgba(0,0,0,.15);
}

.styled-table thead tr {
    background-color: #e76d09dc;
    color:#ffffff;
    text-align: left;
}

.styled-table th,
.styled-table td {
    padding:12px 15px;
}

.styled-table tbody tr{
    border-bottom: 1px solid #fffefe;
    
}

.styled-table tbody tr:nth-of-type(even){
    background-color: #f3f3f3;
}

.styled-table tbody tr:last-of-type{
    border-bottom: 2px solid #fffefe;
    border-left: 2px solid #ffffff;
    border-right: 2px solid #ffffff;
}

.btn {
    border:none;
    color: white;
    padding:5px 8px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    border-radius: 5px;
    font-size:16px;
    margin :4px 2px;
    cursor: pointer;
}

.btn-edit{
    background-color: #2c7ad4;
}

.btn-delete{
    background-color: #d63024;
}

.btn-view{
    background-color: #857d7d;
}

.btn-contact{
    background-color:rgb(11, 117, 64);
}


